import { Card, Divider, Tab, Tabs } from "@mui/material";
import { Link, Outlet, useLocation } from "react-router-dom";
import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";


const Container = () => {

    const location = useLocation();

    const task = useTask('Core.manage')

    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Card variant="outlined" sx={{ flexGrow: 1 }}>
                    <Tabs value={location.pathname}>
                        <Tab
                            value='/subscriptions'
                            to={'/subscriptions'}
                            LinkComponent={Link}
                            label={<strong>اشتراک‌های فعال</strong>}
                        />
                        <Tab
                            value='/subscriptions/history'
                            to={'/subscriptions/history'}
                            LinkComponent={Link}
                            label={<strong>تاریخچه پرداخت‌ها</strong>}
                        />
                    </Tabs>
                    <Divider />
                    <Outlet />
                </Card>
            </ResponsiveContainer>
        </TabView>
    )
}

export default Container;